  }
  .text__size_xxxl {
    font-weight: 600;
    font-size: 48px;
    line-height: 59px;
  }
  .header__container {
    padding: 50px 20px 16px 16px;
  }
  .header__nav-container {
    display: none;
  }
  .header__nav-container-mobile {
    display: flex;
  }
  .header__logo {
    font-weight: 600;
    font-size: 18px;
  }
  .footer__box {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer-menu {
    gap: 100px;
  }
  .product__list-content {
    justify-content: center;
    row-gap: 32px;
  }
  .product__image {
    margin-bottom: 9px;
  }
  .product__title {
    margin-bottom: 4px;
  }
  .product__desc {
    margin-bottom: 8px;
  }
  .cart {
    justify-content: center;
  }
  .cart-item {
    display: flex;
    padding-top: 18px;
    padding-bottom: 24px;
  }
  .cart-item__product-image {
    display: none;
  }
  .cart-item__product-desc {
    margin-top: -6px;
  }
  .cart-command-bar__button {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    padding: 8px 16px;
  }
  .cart-form__input {
    margin-right: 0;
  }
